import Vue from 'vue';
import VueRouter from 'vue-router';

import home from '../views/technology/Home.vue';
import Cooperator from '../views/cooperator/cooperator.vue';

// 路由
Vue.use(VueRouter);
const routes = [
  {
    path: '/',
    name: 'Home',
    component: home,
  },
  {
    path: '/cooperator',
    name: 'Cooperator',
    component: Cooperator,
  },
];
const router = new VueRouter({
  mode: 'hash',
  routes,
});

export default router;
