<template>
  <div>
    <section class="technology_banner_area">
      <img
        src="../../../public/img/technology/technology-bg.png"
        alt="可视化运行平台"
      />
    </section>
    <section class="technology_info introduction">
      <div class="container technology">
        <div class="advantage_header">
          <Title :title-name="techtitle"></Title>
        </div>
        <div class="row">
          <template v-for="tech in technologys">
            <div class="col-lg-3 col-sm-6" :key="tech.cnName">
              <div class="thumbnail">
                <img class="techImg" :src="tech.imgUrl" />
                <!-- <img class="techImg" src="/img/technology/technology_01.png" alt="效果非凡" /> -->
                <div class="caption">
                  <p>{{ tech.cnName }}</p>
                  <h5 class="enTitle">{{ tech.enName }}</h5>
                  <h5 class="enDescribe">{{ tech.content }}</h5>
                </div>
              </div>
            </div>
          </template>
        </div>
      </div>
    </section>
    <section class="featurehighlights_info introduction">
      <div class="container">
        <Title :title-name="techtitle"></Title>
        <div class="row funcol">
          <div class="col-lg-8 col-md-12">
            <img
              src="../../../public/img/technology/highlight_01.png"
              alt="功能亮点"
            />
          </div>
          <div class="col-lg-4 col-lg-pull-2">
            <div class="intro_content">
              <h1>基础功能</h1>
            </div>
          </div>
        </div>
        <div class="row funcol">
          <div class="col-lg-4 col-md-12"></div>
          <div class="col-lg-8 col-md-12">
            <img
              src="../../../public/img/technology/highlight_01.png"
              alt="功能亮点"
            />
          </div>
        </div>
        <div class="row funcol">
          <div class="col-lg-8 col-md-12">
            <img
              src="../../../public/img/technology/highlight_01.png"
              alt="功能亮点"
            />
          </div>
        </div>
        <div class="row funcol">
          <div class="col-lg-4 col-md-12"></div>
          <div class="col-lg-8 col-md-12">
            <img
              src="../../../public/img/technology/highlight_01.png"
              alt="功能亮点"
            />
          </div>
        </div>
        <div class="row funcol">
          <div class="col-lg-8 col-md-12">
            <img
              src="../../../public/img/technology/highlight_05.png"
              alt="功能亮点"
            />
          </div>
        </div>
      </div>
    </section>
    <Cooperator></Cooperator>
    <Contact></Contact>
    <FooterTest></FooterTest>
  </div>
</template>
<script>
import axios from 'axios';
import Cooperator from '../cooperator/cooperator';

export default {
  data() {
    return {
      techtitle: '',
      technologys: [],
    };
  },
  mounted() {
    axios
      .get('/data/technology/operatingPlatF.json')
      .then((response) => {
        console.log(response);
        debugger; //eslint-disable-line

        this.technologys = response.data.technologyList;
        this.techtitle = response.data.technologyTitle;
      })
      .catch(function(error) {
        console.log(error);
      });
  },
  components: {
    Cooperator,
  },
};
</script>
